import React from 'react';

import { Link, useLocation } from 'react-router-dom';

import './Navbar.css';

const routes = [
    { path: '/', label: 'Home' },
    { path: '/about', label: 'About' },
    { path: '/projects', label: 'Projects' },
    { path: '/links', label: 'Links' }
];

function Navbar() {
    const location = useLocation();
    return (
        <nav className={ location.pathname !== "/" ?  "foo" : "navOpat" }>
            <input type="checkbox" id="check" />
            <label htmlFor="check" className="checkbtn">
                <i className="fa fa-bars"></i>
            </label>
            { location.pathname === "/" ? <label className="brand">Jzonn</label> : <Link to="/"><label className="brand">Jzonn</label></Link> }
            <ul>
                { routes.map(( {path, label} ) => {
                    return location.pathname !== path && <Link key={label} to={path}><li >{label}</li></Link>
                })}
            </ul>
        </nav>
    );
}

export default Navbar;