import React from 'react';

import styles from './Links.module.css';

function Links() {
    return (
        <div className={styles.links}>
            <a href="foo" className={styles.liens}><i className="fa fa-github"></i> Github</a>
            <a href="foo" className={styles.liens}><i className="fa fa-codepen"></i> Codepen</a>
            <a href="foo" className={styles.liens}><i className="fa fa-reddit"></i> Reddit</a>
            <a href="foo" className={styles.liens}><i className="fa fa-link"></i> Discord</a>
            <a href="foo" className={styles.liens}><i className="fa fa-twitch"></i> Twitch</a>
            <a href="foo" className={styles.liens}><i className="fa fa-youtube"></i> Youtube</a>
            <a href="foo" className={styles.liens}><i className="fa fa-instagram"></i> Instagram</a>
        </div>
    );
}

export default Links;